.mainMenu{
    top: 0%;
    z-index: 99;
    color: white;
    background-color: rgba(0, 0, 0, 0.178);
    position: absolute;
    width: 100%;
    padding-top: 20px;
    display: flex;
    flex-direction: row;
    width: 100%;
}
.divLogo{
    width: 10%;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}
.mainMenu ul{
    width: 90%;
    display: flex;
    flex-direction: row;
    list-style:none;
    justify-content: space-around;
    font-size: 1.5rem;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    padding-bottom: 20px;
    a{
        text-decoration: none;
        color: white;
    }
}
@media screen and (max-width:800px){
    .mainMenu{
    flex-direction: column;
    align-items: center;
    background: rgba(0, 0, 0, 0.719);
    }
    .divLogo{
        width: 100%;
    }
    .mainMenu ul{
        width: 100%;
        margin-top: 20px;
        flex-direction: column;
        align-items: center;
        li{
            margin-bottom: 15px;
        }
    }
}